@use "global.module.scss" as *;

.hasTooltip {
    position: relative;
}

#sidebar {
    display: flex;
    flex-direction: column;
    position: relative;
    min-width: 210px;
}

#mobileContainer {
    background-color: $primaryAccent;
    position: fixed;
    top: 105px;
    left: -240px;
    padding: 9px 21px 21px 21px;
    z-index: 1;
    border-radius: 15px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
    transition: all 0.1s ease-in-out;
}

#desktopContainer {
    background-color: white;
    padding: 0 0 0 12px;
    z-index: 1;
}


.arrow {
    background-color: $primaryAccent;
    color: white;
    position: absolute;
    top: 0;
    right: -24px;
    padding: 12px;
    font-size: 100%;
    width: 27px;
    height: 27px;
    z-index: -1;
    border-radius: 50%;
}

#feedback {
    background-color: white;
    margin: 12px 0 0 0;
    padding: 12px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    border-radius: 12px;
    box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .18);

    #thumbsUp {
        margin: 0;
    }

    #review {
        margin: 0;
    }

    .feedbackIcon {
        color: $toolColor;
        position: relative;
        font-size: 30pt;
        cursor: pointer;

        &:hover {
            translate: 0 -2px;
            color: $primaryColor;
            transition: all 0.1s ease-in-out;
        }
    }
}