$primaryColor: #007097;
$primaryAccent: #80B1CB;
$toolColor: #ccc;
$lightYellow: #FFFFE9;
$darkGreen: #007815;
$yellow: #F8CE00;

$colors: (
    'primaryColor': #007097,
    'primaryAccent': #80B1CB,
    'toolColor': #ccc,
    'lightYellow': #FFFFE9,
    'darkGreen': #007815,
    'yellow': #F8CE00
);

:export {

    @each $key,
    $value in $colors {
        #{unquote($key)}: $value;
    }
}